import React, { useCallback, useState, ChangeEventHandler } from 'react';
import { LedgerEntry } from 'app/api/ledger';
import { formatForDatepicker } from 'app/utils/general';
import Button from 'app/components/ui/Button';
import FlexRow from 'app/components/ui/FlexRow';
import { useAccountsByEntryType, EntryType } from 'app/pages/ledger/hooks/accountListing';
import FormAutoCompleteSelect from 'app/components/FormAutoCompleteSelect';
import FormLabel from 'app/components/ui/FormLabel';
import LedgerSelectType from './LedgerSelectType';
import { useForm } from 'app/utils/hooks';

const LedgerEntryForm: React.FC<{
  ledgerEntry: Partial<LedgerEntry>;
  onSubmit: (data: Partial<LedgerEntry>) => void;
}> = ({ ledgerEntry = {}, onSubmit }) => {

  const [formData, fieldSetter] = useForm(ledgerEntry);

  const [type, setType] = useState<EntryType>('Custom');

  const [fromAccounts, toAccounts] = useAccountsByEntryType(type);

  return (
    < div >
      <div>
        <LedgerSelectType
          type={ type }
          onSelect={ setType }
        />
      </div>
      <hr />
      <form
        autoComplete="off"
        onSubmit={ (e) => {
          e.preventDefault();
          onSubmit(formData);
        } }
      >
        <FlexRow style={ { flexDirection: 'column', alignItems: 'stretch' } }>
          <FormLabel>
            <div>From Account:&nbsp;</div>
            <FormAutoCompleteSelect
              name="fromAccount"
              value={ formData.fromAccount }
              onChange={ fieldSetter('fromAccount') }
              options={ fromAccounts }
              optionToValue={ (account) => account.name }
              optionToString={ (account) => `${account.name} (${account.category})` }
            />
          </FormLabel>
          <FormLabel>
            <div>To Account:&nbsp;</div>
            <FormAutoCompleteSelect
              name="toAccount"
              value={ formData.toAccount }
              onChange={ fieldSetter('toAccount') }
              options={ toAccounts }
              optionToValue={ (account) => account.name }
              optionToString={ (account) => `${account.name} (${account.category})` }
            />
          </FormLabel>
          <FormLabel>
            <div>Amount:&nbsp;</div>
            <input value={ formData.amount } name="amount" />
          </FormLabel>
          <FormLabel>
            <div>When:&nbsp;{ formData.when }</div>
            <input
              type="date"
              value={ formatForDatepicker(formData.when) }
              onChange={ fieldSetter('when') }
              name="when"
            />
          </FormLabel>
          <FormLabel>
            <div>Where:&nbsp;</div>
            <input
              name="where"
              value={ formData.where }
              onChange={ fieldSetter('where') }
            />
          </FormLabel>
          <FormLabel>
            <div>Notes:&nbsp;</div>
            <input
              name="notes"
              value={ formData.notes }
              onChange={ fieldSetter('notes') }
            />
          </FormLabel>
        </FlexRow>
        <br />
        <Button role="submit">Submit</Button>
      </form>
    </div >
  );
};

export default LedgerEntryForm;