
import React, { useState, useCallback, useContext, useMemo } from 'react';
import { signIn, GoogleUser } from 'app/api/googleApi';
import { googleApiConfigContext } from 'app/contexts/googleApi';

const SignInGuard: React.FC<{}> = ({ children }) => {
  const { clientId } = useContext(googleApiConfigContext);
  const [user, setUser] = useState<null | GoogleUser>(null);

  const signInAction = useCallback(() => {
    if (!clientId) {
      return;
    }
    signIn(clientId).then(
      (googleUser) => {
        setUser(googleUser);
      },
      (err: any) => {
        console.error(err);
      }
    );
  }, [clientId, setUser]);

  const displayName = useMemo(() => {
    return user?.getBasicProfile().getName();
  }, [user]);

  return (
    <div>
      <div>
        { !user && <button onClick={ signInAction }>signin</button> }
      </div>
      { user && <div>{ displayName }</div> }
      { user && children }
    </div>
  );
};

export default SignInGuard;