
import React, { Suspense } from 'react';
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect
} from 'react-router-dom';

import MainContent from './components/ui/MainContent';
import { staticRoutes } from './staticRoutes';
import AuthGuard from './landmarks/AuthGuard';
import NavBar from './landmarks/NavBar';
import BudgetDataProvider from 'app/landmarks/BudgetDataProvider';

const LedgerPage = React.lazy(() => import(
  /* webpackChunkName: "LedgerPage" */
  './pages/ledger/LedgerPage'
));
const DashboardPage = React.lazy(() => import(
  /* webpackChunkName: "DashboardPage" */
  './pages/dash/DashboardPage'
));
const AccountsPage = React.lazy(() => import(
  /* webpackChunkName: "AccountsPage" */
  'app/pages/accounts/AccountsPage'
));

const App = () => {
  return (
    <AuthGuard>
      <Router>
        <NavBar />
        <hr />
        <MainContent>
          <BudgetDataProvider>
            <Suspense fallback={ <div>Loading</div> }>
              <Switch>
                <Route path={ staticRoutes.dash }>
                  <DashboardPage />
                </Route>
                <Route path={ staticRoutes.ledger }>
                  <LedgerPage />
                </Route>
                <Route path={ staticRoutes.accounts }>
                  <AccountsPage />
                </Route>

                <Redirect to={ staticRoutes.dash } />
              </Switch>
            </Suspense>
          </BudgetDataProvider>
        </MainContent>
      </Router>
    </AuthGuard>
  );
};

export default App;