import { filter } from 'lodash';
import { useContext, useMemo } from 'react';
import { accountsContext } from 'app/contexts/data';
import { AccountType, BudgetAccount } from 'app/api/accounts';

export const ENTRY_TYPES = ['Income', 'Expense', 'Transfer', 'Custom'] as const;
export type EntryType = (typeof ENTRY_TYPES)[number];

const filterType = (type: AccountType) => (accounts: BudgetAccount[]) => {
  return filter(accounts, a => a.type === type);
};

const filterExpense = filterType('Expense');
const filterIncome = filterType('Income');
const filterAccount = filterType('Account');

export const useAccountsByEntryType = (entryType: EntryType) => {
  const [accounts] = useContext(accountsContext);

  return useMemo(() => {
    switch (entryType) {
      case 'Expense':
        return [filterAccount(accounts), filterExpense(accounts)];
      case 'Income':
        return [filterIncome(accounts), filterAccount(accounts)];
      case 'Transfer':
        return [filterAccount(accounts), filterAccount(accounts)];
      default:
        return [accounts, accounts];
    }
  }, [accounts, entryType]);
};