import React from 'react';
import { EntryType, ENTRY_TYPES } from '../../hooks/accountListing';

const LedgerSelectType: React.FC<{
  type: EntryType,
  onSelect: (t: EntryType) => void;
}> = ({ type, onSelect }) => {
  return (
    <select
      value={ type }
      style={ { fontSize: '1.25rem' } }
      onChange={ (e) => onSelect(e.target.value as EntryType) }
    >
      { ENTRY_TYPES.map(entryType =>
        <option key={ entryType } value={ entryType }>{ entryType }</option>
      ) }
    </select>
  );
};

export default LedgerSelectType;