
import { hot } from 'react-hot-loader/root';
import React, { useContext } from 'react';
import { accountsContext } from 'app/contexts/data';
import TD from 'app/components/ui/TD';
import DollarTD from 'app/components/ui/DollarTD';

const AccountsPage = () => {
  const [accounts] = useContext(accountsContext);

  return (
    <div>
      <table>
        <thead>
          <tr>
            <th></th>
            <th>Type</th>
            <th>Category</th>
            <th>Starting</th>
            <th>Estimate</th>
            <th>Notes</th>
          </tr>
        </thead>
        <tbody>

          { accounts.map((account) => (
            <tr key={ account.name }>
              <TD>{ account.name }</TD>
              <TD>{ account.type }</TD>
              <TD>{ account.category }</TD>
              <DollarTD>{ account.starting }</DollarTD>
              <DollarTD>{ account.estimate }</DollarTD>
              <TD>{ account.notes }</TD>
            </tr>
          )) }
        </tbody>
      </table>
    </div>
  );
};

export default hot(AccountsPage);