import React, { SelectHTMLAttributes, useState, useMemo } from 'react';
import { filter, includes } from 'lodash';
import DropdownContainer from 'app/components/ui/dropdown/DropdownContainer';
import DropdownMenu from 'app/components/ui/dropdown/DropdownMenu';
import FlexRow from './ui/FlexRow';

type FormAutoCompleteSelectProps<T> = {
  options: T[];
  optionToValue: (v: T) => string;
  optionToString?: (v: T) => string;
} & SelectHTMLAttributes<any>;

const FormAutoCompleteSelect = <T extends any>({
  options,
  optionToValue,
  optionToString = a => a.toString(),
  ...otherProps
}: FormAutoCompleteSelectProps<T>) => {
  const [showOptions, setShowOptions] = useState(false);
  const value = otherProps.value;

  const filteredOptions = useMemo(() => {
    if (value === void 0) {
      return options;
    }
    return filter(options, opt => includes(
      optionToString(opt).toLowerCase(),
      value.toString().toLowerCase()
    ));
  }, [value, options, optionToString]);

  return (
    <div>
      <select
        { ...otherProps }
        style={ { display: 'none' } }
      >
        { options.map(option => (
          <option
            key={ optionToValue(option) }
            value={ optionToValue(option) }
          >{ optionToString(option) }</option>
        )) }
      </select>

      <DropdownContainer>
        <input
          { ...otherProps }
          onFocus={ () => {
            setShowOptions(true);
          } }
          onBlur={ () => {
            setShowOptions(false);
          } }
        />
        <DropdownMenu show={ showOptions }>
          { filteredOptions.length === 0 &&
            <div style={ { padding: '0.25rem 0.5rem' } }>Not Found</div>
          }
          <FlexRow size="0" style={ { flexDirection: 'column', alignItems: 'stretch' } }>
            { filteredOptions.map((opt) => (
              <button
                key={ optionToString(opt) }
                style={ { whiteSpace: 'nowrap', padding: '0.5rem' } }
                role="button"
                value={ optionToValue(opt) }
                onMouseDown={ otherProps.onChange }
              >
                { optionToString(opt) }
              </button>
            )) }
          </FlexRow>
        </DropdownMenu>
      </DropdownContainer>
    </div >
  );
};

export default FormAutoCompleteSelect;