import React, { useMemo } from 'react';
import { LedgerEntry } from 'app/api/ledger';
import { groupBy, mapValues, map, range, values, sum } from 'lodash';
import DollarTD from 'app/components/ui/DollarTD';

const MONTHS = range(12);
const MONTHS_NAMES = [
  'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'
];

const TableSummary: React.FC<{ entries: LedgerEntry[]; }> = ({ entries }) => {

  const tableData = useMemo(() => {
    const byFrom = groupBy(entries, e => e.toAccount);

    const byFromThenMonths = mapValues(byFrom, (byCatEntries) => {
      return mapValues(
        groupBy(byCatEntries, e => new Date(e.when).getMonth()),
        (entries) => {
          return sum(map(values(entries), e => e.amount));
        }
      );
    });

    return byFromThenMonths;
  }, [entries]);

  const monthTotals = useMemo(() => {
    return MONTHS.map(month => sum(
      map(tableData, byMonth => byMonth[month])
    ));
  }, [tableData]);

  const categoryTotals = useMemo(() => {
    return mapValues(tableData, byMonth => sum(values(byMonth)));
  }, [tableData]);

  const grandTotal = useMemo(() => sum(monthTotals), [monthTotals]);

  return (
    <div>
      <div>
      </div>
      <table>
        <thead>
          <tr>
            <td></td>
            <td>Total</td>
            { MONTHS_NAMES.map(n => <td>{ n }</td>) }
          </tr>
        </thead>
        <tbody>
          <tr>
            <th style={ { textAlign: 'right' } }>Total(by month)</th>
            <DollarTD>{ grandTotal }</DollarTD>
            { map(MONTHS, (month) => (
              <DollarTD key={ month }>{ monthTotals[month] || 0 }</DollarTD>
            )) }
          </tr>
          { map(tableData, (catRow, Category) => (
            <tr>
              <th style={ { textAlign: 'right' } }>{ Category }</th>
              <DollarTD>{ categoryTotals[Category] }</DollarTD>
              { map(MONTHS, (month) => (
                <DollarTD key={ month }>{ catRow[month] || 0 }</DollarTD>
              )) }
            </tr>
          )) }
        </tbody>
      </table>
    </div>
  );
};

export default TableSummary;