import React, { TdHTMLAttributes } from 'react';
import TD from 'app/components/ui/TD';

const DollarTD: React.FC<TdHTMLAttributes<any>> = ({ children, style, ...props }) => {
  return (
    <TD style={{ textAlign: 'right', ...style }} {...props}>
      ${children}
    </TD>
  )
};

export default DollarTD;