
import { hot } from 'react-hot-loader/root';
import React, { useContext, useMemo } from 'react';
import FlexRow from 'app/components/ui/FlexRow';
import { ledgerContext } from 'app/contexts/data';
import { useLedgerOrdering, useLedgerTextSearch, useLedgerAmountFilter } from './hooks/ledgerFilters';
import AddLedgerEntry from './components/edit/AddLedgerEntry';
import EditLedgerEntry from './components/edit/EditLedgerEntry';
import RemoveLedgerEntry from './components/edit/RemoveLedgerEntry';
import { flow } from 'lodash';
import SearchBox from 'app/components/SearchBox';
import LedgerAmountFilter from 'app/pages/ledger/components/control/LedgerAmountFilter';
import TD from 'app/components/ui/TD';
import DollarTD from 'app/components/ui/DollarTD';
import CopyLedgerEntry from './components/edit/CopyLedgerEntry';

const LedgerPage = () => {
  const [ledgerEntries, loadSheets] = useContext(ledgerContext);

  const [search, setSearch, applySearch] = useLedgerTextSearch();
  const [order, setOrder, applyOrder] = useLedgerOrdering();
  const [minMax, setMinMax, applyAmount] = useLedgerAmountFilter();

  const displayEntries = useMemo(() => {
    return flow(applySearch, applyAmount, applyOrder)(ledgerEntries);
  }, [ledgerEntries, applySearch, applyOrder, applyAmount]);

  return (
    <div>
      <div>
        <SearchBox search={ search } onSearch={ setSearch } placeholder="search" />
        <LedgerAmountFilter minMax={ minMax } setMinMax={ setMinMax } />
      </div>
      <hr />
      <FlexRow>
        <div style={ { flex: 1 } }></div>
        <AddLedgerEntry onAdd={ loadSheets } />
      </FlexRow>
      <table>
        <thead>
          <tr>
            <th></th>
            <th>From Account</th>
            <th>To Account</th>
            <th>Amount</th>
            <th>When</th>
            <th>Where</th>
            <th>Notes</th>
          </tr>
        </thead>
        <tbody>

          { displayEntries.map((entry) => (
            <tr key={ entry.rowId }>
              <TD>{ entry.rowId }</TD>
              <TD>{ entry.fromAccount }</TD>
              <TD>{ entry.toAccount }</TD>
              <DollarTD>{ entry.amount }</DollarTD>
              <TD>{ entry.when }</TD>
              <TD>{ entry.where }</TD>
              <TD>{ entry.notes }</TD>
              <TD><CopyLedgerEntry ledgerEntry={ entry } onCopy={ loadSheets } /></TD>
              <TD><EditLedgerEntry ledgerEntry={ entry } onEdit={ loadSheets } /></TD>
              <TD><RemoveLedgerEntry ledgerEntry={ entry } onRemove={ loadSheets } /></TD>
            </tr>
          )) }
        </tbody>
      </table>
    </div>
  );
};

export default hot(LedgerPage);