
import { memoize } from 'lodash';

const init = memoize((clientId: string) => {
  return new Promise((resolve, reject) => {
    gapi.load('client:auth2', () => {
      gapi.client.init({
        clientId: clientId,
        scope: 'https://www.googleapis.com/auth/spreadsheets',
        discoveryDocs: ["https://sheets.googleapis.com/$discovery/rest?version=v4"]
      }).then(resolve, reject);
    });
  });
});

export const sheetsApi = (clientId: string) => {
  return init(clientId).then(() => {
    return new Promise((resolve) => {
      return gapi.client.load('sheets', 'v4', () => {
        resolve();
      });
    });
  });
};

export type GoogleUser = gapi.auth2.GoogleUser;

export const signIn = async (clientId: string): Promise<GoogleUser> => {
  await init(clientId);

  const auth2 = gapi.auth2.getAuthInstance();
  return auth2.signIn();
};
