import React from 'react';
import { useForm } from 'app/utils/hooks';
import FlexRow from 'app/components/ui/FlexRow';
import FormLabel from 'app/components/ui/FormLabel';
import { GoogleApiConfig } from 'app/contexts/googleApi';

const ConfigForm: React.FC<{
  config: GoogleApiConfig;
  onSubmit: (c: GoogleApiConfig) => void;
}> = ({ config, onSubmit }) => {

  const [formData, fieldSetter] = useForm(config);

  return (
    <form
      onSubmit={ (e) => {
        e.preventDefault();
        onSubmit(formData as GoogleApiConfig);
      } }
    >
      <FlexRow style={ { flexDirection: 'column', alignItems: 'stretch' } }>
        <FormLabel>
          <span>Client Id</span>
          <input
            required
            value={ formData.clientId || '' }
            onChange={ fieldSetter('clientId') }
          />
        </FormLabel>
        <FormLabel>
          <span>Spreadsheet Id</span>
          <input
            required
            value={ formData.spreadsheetId || '' }
            onChange={ fieldSetter('spreadsheetId') }
          />
        </FormLabel>
        <button>Apply</button>
      </FlexRow>
    </form>
  );
};

export default ConfigForm;

