
import React from 'react';

const Modal: React.FC<{ isOpen: boolean, close?: () => void; }> = ({
  isOpen,
  close,
  children,
}) => {
  return (isOpen || null) && (
    <div style={ {
      position: 'fixed',
      zIndex: 1000,
      top: 0,
      left: 0,
      height: '100vh',
      width: '100vw',
      overflow: 'scroll',
      backgroundColor: 'rgba(0,0,0,0.7)'
    } }>
      <div style={ {
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center'
      } }>
        <div style={ {
          margin: '10vh 0',
          maxWidth: '80%',
          minWidth: '50%',
          backgroundColor: 'white',
          padding: '1rem',
        } }>
          { children }
        </div>
      </div>
    </div>
  );
};

export default Modal;