import { hot } from 'react-hot-loader/root';
import React, { useState, useEffect } from 'react';
import FlexRow from 'app/components/ui/FlexRow';
import { useLocalStorage, useToggle } from 'app/utils/hooks';
import ConfigForm from './components/ConfigForm';
import { googleApiConfigContext, GoogleApiConfig } from 'app/contexts/googleApi';

const SheetsConfigGuard: React.FC<{}> = ({ children }) => {
  const [ready, setReady] = useState(true);

  const [config, setConfig] = useLocalStorage<GoogleApiConfig>('google-sheets-api-config', {});

  const [show, toggleShow] = useToggle(!config?.clientId || !config?.spreadsheetId);

  useEffect(() => {
    setReady(false);
    window.setTimeout(() => {
      setReady(true);
    }, 60);
  }, [config?.clientId, config?.spreadsheetId]);

  return (
    <div>
      <FlexRow>
        <button onClick={ toggleShow }>Configure</button>
      </FlexRow>
      { show &&
        <ConfigForm
          config={ config || {} }
          onSubmit={ setConfig }
        />
      }
      <hr />
      <googleApiConfigContext.Provider value={ config }>
        { config?.clientId && config?.spreadsheetId && ready && children }
      </googleApiConfigContext.Provider>
    </div>
  );
};

export default hot(SheetsConfigGuard);