
import React from 'react';
import SheetsGuard from './sheetsGuard/SheetsGuard';
import SignInGuard from './SignInGuard';

const AuthGuard: React.FC<{}> = ({ children }) => {
  return (
    <SheetsGuard>
      <SignInGuard>
        { children }
      </SignInGuard>
    </SheetsGuard>
  );
};

export default AuthGuard;