import { sheetsApi } from 'app/api/googleApi';
import { map } from 'lodash';

export type AccountType = 'Income' | 'Account' | 'Expense';

export type BudgetAccount = {
  type: AccountType;
  name: string;
  category: string;
  estimate: number;
  starting: number;
  notes: string;
};

export const getAccounts = async (clientId: string, spreadsheetId: string) => {
  await sheetsApi(clientId);

  const response = await gapi.client.sheets.spreadsheets.values.batchGet({
    spreadsheetId: spreadsheetId,
    ranges: [
      'Expense!A:E',
      'Income!A:C',
      'Accounts!A:C',
    ],
    valueRenderOption: 'FORMATTED_VALUE'
  });

  const [expense, income, accounts] = response.result.valueRanges || [];

  const expenseAccounts = map(expense.values?.slice(1), values => {
    return {
      type: 'Expense',
      name: values[0],
      category: values[1],
      estimate: values[2],
      starting: 0,
      notes: values[4] || '',
    } as BudgetAccount;
  });

  const incomeAccounts = map(income.values?.slice(1), values => {
    return {
      type: 'Income',
      name: values[0],
      category: 'Income',
      estimate: values[1],
      starting: 0,
      notes: values[2] || '',
    } as BudgetAccount;
  });

  const accountAccounts = map(accounts.values?.slice(1), values => {
    return {
      type: 'Account',
      name: values[0],
      category: values[1],
      estimate: 0,
      starting: values[2],
      notes: values[2] || '',
    } as BudgetAccount;
  });


  return [
    ...expenseAccounts,
    ...incomeAccounts,
    ...accountAccounts,
  ] as BudgetAccount[];
};