
import { filter, some, values, includes, orderBy, pick } from "lodash";
import { useState, useCallback } from "react";
import { LedgerEntry } from 'app/api/ledger';

type LedgerProcessorHook<T = any> = [
  T,
  (v: T) => void,
  (e: LedgerEntry[]) => LedgerEntry[],
];

export const useLedgerTextSearch = (): LedgerProcessorHook<string> => {
  const [ search, setSearch ] = useState('');
  
  const applyFilter = useCallback<LedgerProcessorHook[2]>((entries) => {
    if (!search) {
      return entries.slice();
    }
    return filter(entries, entry => {
      return some(
        values(pick(entry, ['fromAccount', 'toAccount', 'when', 'where', 'notes'])), 
        val => val && includes(val.toLowerCase(), search.toLowerCase())
      );
    });
  }, [search]);

  return [ 
    search,
    setSearch,
    applyFilter,
  ];
};

export const useLedgerAmountFilter = (): LedgerProcessorHook<[ number, number ]> => {
  const [ minMax, setMinMax ] = useState<[number, number]>([ 0, Infinity ]);
  
  const applyFilter = useCallback<LedgerProcessorHook[2]>((entries: LedgerEntry[]) => {
    return filter(entries, entry => {
      return entry.amount >= minMax[0] && entry.amount <= minMax[1];
    });
  }, [ minMax ]);

  return [ 
    minMax,
    setMinMax,
    applyFilter,
  ];
};

type OrderTuple = [ keyof LedgerEntry, 'asc' | 'desc'];

export const useLedgerOrdering = (): LedgerProcessorHook<OrderTuple> => {
  const [ order, setOrder ] = useState<OrderTuple>(['rowId', 'desc']);
  
  const applyOrder = useCallback<LedgerProcessorHook[2]>((entries: LedgerEntry[]) => {
    return orderBy(entries, entry => entry[order[0]], order[1])
  }, [order]);

  return [ 
    order,
    setOrder,
    applyOrder,
  ];
}

