
export const getFormFields = <T extends {}>(form: HTMLFormElement) => {
  const formData = new FormData(form);
  const data: { [k: string]: FormDataEntryValue; } = {};
  formData.forEach((v, k) => {
    data[k] = v;
  });
  return data as T;
};

export const parseAmount = (amount: string) => {
  return parseFloat(amount.replace('$', '').replace(/,/g, ''));
};

export const formatForDatepicker = (date?: string): string => {
  if (date) {
    const [m, d, y] = date.split('/');
    return `${y}-${m}-${d}`;
  } else {
    const d = new Date();
    return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
  }
};