import { useState, useCallback, useEffect, ChangeEventHandler } from "react";

export const useToggle = (init: boolean) => {
  const [isOn, setIsOn] = useState<boolean>(init);

  const toggle = useCallback(() => {
    setIsOn(!isOn);
  }, [isOn, setIsOn]);

  return [isOn, toggle] as const;
};

type UseLocalStorage = {
  <T>(key: string): readonly [T | null, (val: T) => void];
  <T>(key: string, defaultValue: T): readonly [T, (val: T) => void];
};

export const useLocalStorage: UseLocalStorage = <T = string>(
  key: string,
  defaultValue?: T
) => {
  const [state, setState] = useState<T | null>(() => {
    const val = JSON.parse(localStorage.getItem(key) || 'null');
    return defaultValue === void 0 ? val : (val || defaultValue);
  });

  useEffect(() => {
    localStorage.setItem(key, JSON.stringify(state));
  }, [state]);

  return [state, setState] as const;
};

export const useForm = <T>(initial: Partial<T>) => {

  const [formData, setFormData] = useState<Partial<T>>(initial);

  const fieldSetter = useCallback((field: keyof T): ChangeEventHandler<HTMLInputElement> => {
    return (e) => {
      setFormData({
        ...formData,
        [field]: e.target.value
      });
    };
  }, [formData, setFormData]);

  return [formData, fieldSetter] as const;
};