import React, { useCallback, useContext } from 'react';
import Button from 'app/components/ui/Button';
import { addSheetEntry } from 'app/api/ledger';
import { useToggle } from 'app/utils/hooks';
import LedgerEntryForm from '../form/LedgerEntryForm';
import Modal from 'app/components/Modal';
import { googleApiConfigContext } from 'app/contexts/googleApi';

const AddLedgerEntry: React.FC<{

  onAdd: () => void;
}> = ({ onAdd }) => {
  const { clientId, spreadsheetId } = useContext(googleApiConfigContext);
  const addEntry = useCallback((data) => {
    if (!clientId || !spreadsheetId) {
      return;
    }
    addSheetEntry(clientId, spreadsheetId, data).then(onAdd);
  }, [onAdd]);

  const [showForm, toggleShowForm] = useToggle(false);

  return (
    <div>
      <Button onClick={ toggleShowForm }>
        Add Entry
      </Button>
      <Modal isOpen={ showForm }>
        <LedgerEntryForm onSubmit={ addEntry } />
        <Button onClick={ toggleShowForm }>
          Cancel Add
        </Button>
      </Modal>
    </div>
  );
};

export default AddLedgerEntry;