import { sheetsApi } from 'app/api/googleApi';
import { parseAmount } from 'app/utils/general';

export type LedgerEntry = {
  rowId: number;
  fromAccount: string;
  toAccount: string;
  amount: number;
  when: string;
  where: string;
  notes: string;
};

type LedgerValues = [string, string, string, string, string, string];

const toEntry = (
  rowId: number,
  values: LedgerValues
): LedgerEntry => {
  return {
    rowId,
    fromAccount: values[0],
    toAccount: values[1],
    amount: parseAmount(values[2]),
    when: values[3],
    where: values[4],
    notes: values[5],
  } as LedgerEntry;
};

const fromEntry = (entry: LedgerEntry | Omit<LedgerEntry, 'rowId'>): LedgerValues => {
  const { fromAccount, toAccount, amount, when, where, notes } = entry;
  return [fromAccount, toAccount, `${amount}`, when, where, notes];
};

export const getSheet = async (clientId: string, spreadsheetId: string) => {
  await sheetsApi(clientId);

  const response = await gapi.client.sheets.spreadsheets.values.get({
    spreadsheetId: spreadsheetId,
    range: 'Ledger!A:F',
    valueRenderOption: 'FORMATTED_VALUE'
  });

  if (!response.result.values) {
    return [];
  }

  const data = response.result.values.slice(1);

  return data.map((values, rowIndex) => {
    return toEntry(
      rowIndex + 2, //1 is header,
      values as LedgerValues
    );
  }) as LedgerEntry[];
};

export const addSheetEntry = async (
  clientId: string,
  spreadsheetId: string,
  entry: Omit<LedgerEntry, 'rowId'>
) => {
  await sheetsApi(clientId);

  const values = [fromEntry(entry)];

  const result = await gapi.client.sheets.spreadsheets.values.append({
    spreadsheetId: spreadsheetId,
    range: 'Ledger',
    valueInputOption: 'USER_ENTERED',
    resource: { values }
  });

  return result;
};

export const editSheetEntry = async (
  clientId: string,
  spreadsheetId: string,
  rowId: number,
  entry: Omit<LedgerEntry, 'rowId'>
) => {
  await sheetsApi(clientId);

  const values = [fromEntry(entry)];

  const result = await gapi.client.sheets.spreadsheets.values.update({
    spreadsheetId: spreadsheetId,
    range: `Ledger!A${rowId}:F${rowId}`,
    valueInputOption: 'USER_ENTERED',
    resource: { values }
  });

  return result;
};

export const removeSheetEntry = async (
  clientId: string,
  spreadsheetId: string,
  rowId: number
) => {
  await sheetsApi(clientId);

  const request = {
    requests: [
      {
        'deleteDimension': {
          'range': {
            'sheetId': 1657640550, //Ledger gid
            'dimension': 'ROWS',
            'startIndex': rowId - 1, //rowId is 1 based
            'endIndex': rowId
          }
        }
      }
    ]
  };

  const result = await gapi.client.sheets.spreadsheets.batchUpdate({
    spreadsheetId: spreadsheetId,
  }, request);

  return result;
};