
import React, { useState, useEffect } from 'react';
import Button from 'app/components/ui/Button';
import FlexRow from 'app/components/ui/FlexRow';

const LedgerAmountFilter: React.FC<{
  minMax: [number, number],
  setMinMax: (minMax: [number, number]) => void,
}> = ({ minMax, setMinMax }) => {
  const [localMin, setLocalMin] = useState(minMax[0]);
  const [localMax, setLocalMax] = useState(minMax[1]);

  useEffect(() => {
    setMinMax([localMin || 0, localMax || Infinity]);
  }, [localMin, localMax]);

  return <>
    <label>
      <FlexRow>
        <span>Min</span>
        <input
          type="number"
          value={localMin}
          onChange={(e) => setLocalMin(parseInt(e.target.value))}
        />
        <Button onClick={() => {
          setLocalMin(0);
        }}>Clear Min</Button>
      </FlexRow>
    </label>
    <label>
      <FlexRow>
        <span>Max</span>
        <input
          type="number"
          value={localMax}
          onChange={(e) => setLocalMax(parseInt(e.target.value))}
        />
        <Button onClick={() => {
          setLocalMax(Infinity);
        }}>Clear Max</Button>
      </FlexRow>
    </label>
  </>;
};

export default LedgerAmountFilter;