import React, { useCallback, useContext } from 'react';
import Button from 'app/components/ui/Button';
import { removeSheetEntry, LedgerEntry } from 'app/api/ledger';
import { googleApiConfigContext } from 'app/contexts/googleApi';

const RemoveLedgerEntry: React.FC<{
  ledgerEntry: LedgerEntry;
  onRemove: () => void;
}> = ({
  ledgerEntry,
  onRemove
}) => {
    const { clientId, spreadsheetId } = useContext(googleApiConfigContext);
    const removeEntry = useCallback(() => {
      if (!clientId || !spreadsheetId) {
        return;
      }
      removeSheetEntry(clientId, spreadsheetId, ledgerEntry.rowId).then(onRemove);
    }, [ledgerEntry.rowId, onRemove]);

    return (
      <div>
        <Button onClick={ removeEntry }>
          Remove
      </Button>
      </div>
    );
  };

export default RemoveLedgerEntry;